import { Table, Modal, Space, message } from "antd";
import { useEffect } from "react";
import { writeTextToClipboard } from "../utils";
import { deleteItemApi } from "../service";

interface Props {
  onUpdate: () => void;
  dataSource: { url: string; filePath: string }[];
}
const Demo = (props: Props) => {
  const { onUpdate, dataSource } = props;

  const onDelete = (url: string) => {
    deleteItemApi(url)
      .then(() => {
        message.success("");
      })
      .catch((err) => message.error(err.message));
  };

  const columns = [
    {
      title: "",
      dataIndex: "url",
      key: "url",
      render: (text: string) => <a>{text}</a>,
    },
    {
      title: "",
      dataIndex: "filePath",
      key: "filePath",
    },
    {
      title: "Action",
      key: "action",
      width: 100,
      render: (text: string, record: any) => (
        <Space size="middle">
          <a onClick={() => writeTextToClipboard(record.filePath)} key="copy">
            copy
          </a>
          <a onClick={() => onDelete(record.url)}>delete</a>
        </Space>
      ),
    },
  ];

  useEffect(() => onUpdate(), []);

  return (
    <div>
      <Table
        size="small"
        columns={columns}
        dataSource={dataSource}
        scroll={{ y: "calc(100vh - 150px)" }}
        pagination={false}
      />
      <Modal></Modal>
    </div>
  );
};

export default Demo;
